interface LinkProps {
    to: string,
    style?: React.CSSProperties
}
export const ExternalLink = ({ children, to, style }: PropsWithChildren<LinkProps>) => {

    return (
        <Link to={to} style={{
            textDecoration: 'none',
            color: 'inherit',
            fontSize: '1.8svw',
            fontWeight: '600',
            ...style
        }}>
            {children} <MdOutlineArrowOutward style={{ scale: '1.4', transform: 'translateY(5%)' }} /></Link>
    )
}

export const Disk = styled.div`
    height: 102svh;
    aspect-ratio: 1/1;
    background-image: url(${disk});
    background-size: cover;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(-50%,-50%);
    z-index: 0;

    &::before{
        content:'';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-color: rgba(204, 51, 39, 1);
        width: 33.5%;
        aspect-ratio: 1/1;
        border-radius: 100%;
    }

    &::after{
        content:'';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-color: #000000;
        width: 2%;
        aspect-ratio: 1/1;
        border-radius: 100%;
        z-index: 1;
    }
`