import React from 'react'
import FadeInComponent from '../FadeInComponent'
import { styled } from 'styled-components'
import coursePhoto from '../../img/main/Course.webp'
import { Link } from 'react-router-dom'
import { Spacer } from '../PageBlocks'

const CourseCarouselElement = styled(FadeInComponent)`
    width: 94%;
    display: flex;
    justify-content: flex-end;
    margin-bottom: 16.6svh;
`

interface Course {
    photo: string,
    title: string,
    description: string,
    id: number
}
const courses: Course[] = [
    {
        photo: coursePhoto,
        title: 'как написать песню',
        description: 'От идеи до реализации',
        id: 1
    }
]

const CourseCard=styled.div`
    width: 50%;
`

const CoursePhoto=styled.img`
width: 100%;
aspect-ratio: 1/1;
object-fit: cover;
`

const CourseTitle=styled.a`
font-size: 3.3svw;
color: rgba(204, 51, 39, 1);
font-weight: 600;
display: block;
`

const CourseDescription=styled.a`
font-size: 1.8svw;
color: rgba(235, 235, 235, 1);
font-weight: 400;
display: block;
`

interface CourseProps{
    course: Course
}

const LinkBlock=styled.div`
height: 4.9svw;
width: 13.5svw;
display: flex;
justify-content: center;
align-Items: center;
border: 0.3svw solid rgba(204, 51, 39, 1);

a{
    display: block;
    color: rgba(204, 51, 39, 1);
    font-Size: 1.8svw;
    font-Weight: 800;
}
`
function Course({course}: CourseProps) {
    const {title, description, photo, id}=course;
    return (
        <CourseCard>
            <CoursePhoto src={coursePhoto} alt='course'/>
            <Spacer/>
            <CourseTitle>{title.toUpperCase()}</CourseTitle>
            <CourseDescription>{description}</CourseDescription>
            <Spacer/>
            <Link to={`/academy/course/${id}`} style={{textDecoration: 'none'}}>
            <LinkBlock>
                <a>ПЕРЕЙТИ</a>
            </LinkBlock>
            </Link>
        </CourseCard>
    )
}

const CourseCarousel = () => {

    return (
        <CourseCarouselElement type='div' threshold={0.5}>
            {
                courses.map(course=><Course course={course}/>)
            }
        </CourseCarouselElement>
    )
}


export default CourseCarousel